<template>
  <div data-app class="table-responsive">
      <label style="font-size:3em">Handling Requests</label>
        <v-data-table
            v-if="loaded"
            :headers="headers"
            :items="pooldata"
            class="elevation-1 vertical"
            :rows-per-page-items="[8,50]"
        >
            <template v-slot:items="props">
                
               <td style='width:3em!important;height:3em!important;font-size:5em!important;text-align:center'>{{ props.item.item_id }}</td>
               <td style='width:40em!important;font-size:2em!important'>
                    <table>
                        <tr style="height:66%!important;background-color:rgba(0,0,0,0)!important;border-bottom:solid 0.1em #D9D9D9"><td colspan="2">{{ props.item.Description }}</td></tr>
                        <tr style="height:1em!important">
                            <td>Requested by : 
                                {{ props.item.Req_Name }}
                            </td>
                            <td>Location : 
                                {{ props.item.Location }}
                            </td>
                        </tr>
                    </table>
                </td>    
                <td style='width:3em!important;height:3em!important;text-align:center' v-html="props.item.newHandler">{{ props.item.newHandler }}</td>
            
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>    
            </template>
        </v-data-table>
    </div>
</template>
    
<script>
export default {
     data(){
        return{
            response_member:[],  
            userdata:[],
            pooldata:[],
            headers: [
          { text: 'ID',value :'id',sortable: false},
          { text: 'Details',value:'name',sortable: false},
          { text: 'Assigned To',value:'username',sortable: false},
        ], 
        loaded:false,
        }
    }, 
    created(){
        setTimeout( () => this.$router.push('Console_display_Unhandled'), 15000);
    },
    async mounted(){      
        await axios.get('RequestPortal/backend/console_handling.php')
        .then(
            (response)=>{
                this.pooldata = response.data;   
                this.pooldata.Description = response.data.Description;
                this.pooldata.Req_Name = response.data.Req_Name;
                this.pooldata.Location = response.data.Location;
                this.pooldata.Handler = response.data.Handler;
                for(var i=0; i<response.data.length;i++){
                    this.pooldata[i].newHandler = this.pooldata[i].Handler.replace(/,/g,"<br/>");
                }
                this.loaded = true;            
            }
        )        
    }  
}
</script>



